
@import url(./assets/style/reset.css);
html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
}
html #app, body #app {
    width: 100%;
    height: 100%;
    position: relative;
    background: #f0f0f0;
}
html #app .header-container, body #app .header-container {
      position: fixed;
      height: 60px;
      background: #09c;
      top: 0;
      left: 0;
      right: 0;
      z-index: 1;
}
html #app .content-container, body #app .content-container {
      width: 100%;
      height: calc(100% - 60px);
      margin-top: 60px;
}



